import React, { useState } from 'react'

export default function slider({data}) {
  const [i, setI] = useState(0)
  const [x, setX] = useState(0)

  return (
    <div className="slider">
      <div className="arrow left" onClick={() => {
        i--;
        if(i<0) i = data.length-1;
        setX(i * -100)
      }}></div>
      <div className="arrow right" onClick={() => {
        i++;
        if(i>=data.length) i = 0;
        setX(i * -100)
      }}></div>
      <div className="slider-wrap">
      {data && data.map((each, idx) => (
        <div key={'slide'+idx} className="slide" style={{left:idx*100+'%'}} >
          <img src={each}/>
        </div>
      ))}
      </div>
      <style jsx>{`
      .arrow {
        position:absolute;
        top:50%;
        z-index:9;
        margin-top:-30px;
        width:60px;
        height:60px;
        border-top:2px solid #FF7575;
      }
      .arrow.left {
        border-left:2px solid #FF7575;
        left:3%;
        transform:rotate(-45deg)
      }
      .arrow.right {
        border-right:2px solid #FF7575;
        right:3%;
        transform:rotate(45deg)
      }

      .slider {
        margin:36px;
        position:relative;
        overflow:hidden;
      }
      img{
        vertical-align:top;
      }
      .slide{
        position:absolute;
        top:0;
        width:100%;
        z-index:1;
      }
      .slide:first-child {
        position:static;
      }
      .slider-wrap{
        transition: transform 0.6s ease;
        transform: translateX(${x}%);
      }
      @media only screen and (max-width: 960px)  {
      }
      `}</style>
    </div>
  )
}